import React from 'react'
import useForm from "./useForm"
import validateInfo from "./ValidateInfo"
import "./Form.css"

function FormSignup(submittedForm) {
    const {handleChange,values,handleSubmit,error}=useForm(validateInfo,submittedForm);
    return (
         <div className="form-content-right">
             <form className="form" onSubmit={handleSubmit}>
                 <h1>Get Started with us today!!Create ur Account</h1>
                <div className="from-inputs">
                    <label htmlFor="username" className="form-label">Name
                    </label>
                    <input type="text"
                         className="form-input"
                         placeholder="Enter ur name"
                         name="username"
                         value={values.username}
                         onChange={handleChange}
                        autoComplete='off'
                    />
                    {error.username && <p>{error.username}</p>}
                </div>

              

                <div className="from-inputs">
                    <label htmlFor="email" className="form-label">Email
                    </label>
                    <input type="email"
                         className="form-input"
                         placeholder="Enter ur email"
                         name="email"
                         value={values.email}
                         onChange={handleChange}
                         autoComplete='off'
                       
                    />
                      {error.email && <p>{error.email}</p>}
                </div>

                <div className="from-inputs">
                    <label htmlFor="password" className="form-label"> Password
                    </label>
                    <input type="password"
                         id="password"
                         name="password"
                         className="form-input"
                         placeholder="Enter ur password"
                         value={values.password}
                         onChange={handleChange}
                          
                    />
                     {error.password && <p>{error.password}</p>}
                </div>

                <div className="from-inputs">
                    <label htmlFor="password" className="form-label"> ConfirmPassword
                    </label>
                    <input type="password"
                            id="password2"
                            name="password2"
                         className="form-input"
                         placeholder="Enter ur password"
                         value={values.password2}
                         onChange={handleChange}
                    />
                     {error.password2 && <p>{error.password2}</p>}
                </div>


                <button type="submit">SignUp</button>
                <span className="form-input-login">Already have an account? <a href="#">LogIn</a></span>
             </form>
            
         </div>
    )
}

export default FormSignup
